import * as yup from "yup";

export const signUpValidationSchema = yup.object().shape({
  firstName: yup
  .string()
  .required("*      "),
  lastName: yup
  .string()
  .required("*       "),
  email: yup
    .string()
    .email("*         ")
    .required("*      "),
  password: yup
    .string()
    .required("*      ")
    .min(4, "*        ")
    .max(15, "*         "),
  confirmPassword: yup.string().required().oneOf([yup.ref("password"), null]),
});
